:root {
  font-size: 16px !important;
  @media screen and (max-width: 760px) {
    font-size: 12px !important;
  }
}
$primary: #ff1e56;

body {
  font-family: 'Manrope', sans-serif;
  background: #FBFBFB;
  scroll-behavior: smooth;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Manrope', sans-serif !important;
}
.nav-link {
  color: #0f2027 !important;
  font-weight: 400;
  font-size: 1rem;
  cursor: pointer;
  letter-spacing: 1px;
}
.additional-headline {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.modal {
  .modal-content {
    box-shadow: none;
    border: none;
    border-radius: 0.5rem;
    th {
      width: 30%;
    }
    input{
      padding-left: 0.5rem;
    }
  }
}
body.modal-open {
  overflow: hidden;
}

header {
  background: #fff;
}
Nav {
  height: 10vh;
  & * {
    font-size: 1rem;
  }
  .navbar-brand {
    font-weight: bold;
    color: $primary;
  }
  .nav-link {
    font-weight: 400;
  }
  .navbar-toggler > img {
    margin: auto;
    width: 1.5rem;
  }
}

.header-headline {
  .container {
    height: 90vh;
    display: flex;
    & > * {
      flex: 1;
      margin: auto;
    }
    img {
      width: 100%;
    }
  }
  h1 {
    font-size: 2rem;
    font-weight: 600;
    color: $primary
  }
  h3 {
    @media screen and (min-width: 760px) {
      width: 75%;
    }
    font-size: 1rem;
  }
}

.pages {
  .nav-link {
    font-weight: bold;
  }
}
.page {
  .no-style {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    transition: 0.2s ease;
    &:hover {
      filter: opacity(0.7);
    }
  }
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
  .card-img-top {
    width: 65%;
    margin: auto;
    padding-top: 1rem;
  }
  .card-body {
    & > * {
      flex:1;
      text-align: center;
    }

    display: flex;
    flex-direction: column;
    .price::before {
      content: '';
      font-size: 0.8rem;
      position: relative;
      bottom: 1.25em;
    }
    .price {
      margin: auto auto 0;
      margin-bottom: 0.5rem;
      font-size: 2rem;
      font-weight: bold;
    }
    .card-title {
      font-weight: bold;
    }
    .bottom-card {
      display: flex;
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 760px){
  Nav {
    height: auto;
  }
  .header-headline {
    .container {
      display: block;
      height: auto;
      padding: 3rem;
    }
    h1, h3 {
      text-align: center;
    }
    .headline {
      margin-bottom: 3rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .btn {
      width: auto;
    }
  }
  .page {
    grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));

  }
}